import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {MusicArtistList} from './page/MusicArtistList'
import {AddMusicArtist} from './page/AddMusicArtist'
import MusicArtistScreens from './Screens'
// import { EditMusicArtist } from './page/EditMusicArtist'

const MusicArtistRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={MusicArtistScreens.MUSICARTIST_LIST.PATH} component={MusicArtistList} />
      <Route path={MusicArtistScreens.ADD_MUSICARTIST.PATH} component={AddMusicArtist} />
      {/* <Route path={MusicArtistScreens.EDIT_MUSICARTIST.PATH} component={EditMusicArtist} /> */}
      <Redirect from='/musicArtist' exact={true} to={MusicArtistScreens.MUSICARTIST_LIST.PATH} />
      <Redirect to={MusicArtistScreens.MUSICARTIST_LIST.PATH} />
    </Switch>
  )
}

export default MusicArtistRoutes
