import {BaseResponse, BaseResponse2, Pagination2} from 'src/app/models/api.types'
import {MusicArtist} from '../models/musicArtist'
import axios from 'axios'

export const url = `/api/v1/musician`

type MusicArtistListParam = {
  page: number
  limit: number
  search?: string
  statuses?: string[]
  // role_id?: string
  // cancelToken?: any
}

export function createMusicArtist(
  description: string,
  status: number,
  logo: string,
  detail: string,
  genre_ids: string
) {
  return axios.post(url, {description: description, status: status})
}

export function updateMusicArtist(id: string, description: string, status: number) {
  return axios.put(url + `/${id}`, {description: description, status: status})
}

export function deleteMusicArtist(id: string) {
  return axios.delete(url + `/${id}`)
}

export function getMusicArtistDetail(id: string) {
  return axios.get<BaseResponse<MusicArtist>>(url + `/${id}`)
}

export function getMusicArtistList({
  page,
  limit,
  search,
  statuses,
}: // role_id,
// cancelToken,
MusicArtistListParam) {
  return axios.get<BaseResponse2<Pagination2<MusicArtist>>>(url, {
    params: {
      page,
      limit,
      search,
      // role_id,
      statuses,
    },
    // cancelToken,
  })
}
